@import "../styles/settings";

.message-flow {
  display: flex;
  justify-content: flex-start;
  height: 100%;
}

.message-flow__palette {
  display: flex;
  flex: 0 0 $palette-width;
  flex-direction: column;
  height: 100%;
}

.message-flow__flow-exerciser {
  flex: 1 1 100%;
  width: 100%;
  overflow: hidden;
  background-color: $ui-background;
}

.message-flow__palette-section {
  @include carbon--type-style('productive-heading-01');

  padding: $spacing-04 0 $spacing-04 $spacing-05;
  background: $ui-background;
}

.message-flow__palette-accordion {
  flex: 1 1 100%;
  height: calc(100% - #{$spacing-08});
  overflow-x: hidden;
  overflow-y: auto;
}

.message-flow__palette-accordion-item {
  // stylelint-disable-next-line declaration-no-important
  border: 0 !important;

  box-shadow: inset 0 -1px $ui-03;

  .bx--accordion__heading {
    height: $spacing-08;

    // background-color: $ui-background;
  }

  &.bx--accordion__item--active .bx--accordion__content {
    border-bottom: 1px solid $ui-03;
  }
}

.message-flow__palette-title {
  display: flex;
  align-items: center;
  height: $spacing-06;
}

.message-flow__palette-icons {
  position: relative;
  width: $spacing-06;
  height: $spacing-06;

  .palette--icon-size--large & {
    width: $spacing-07;
    height: $spacing-07;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
  }

  .message-flow__palette-open {
    visibility: hidden;

    .bx--accordion__item--active & {
      visibility: visible;
    }
  }
}

.palette__tree-item {
  height: 32px;
  padding-bottom: 0;
}

.message-flow__palette-label {
  @include carbon--type-style('label-01');

  margin-left: $spacing-03;
}

.message-flow__palette-accordion-item .bx--accordion__content {
  padding: 0;
  // padding-right: 0;
  // padding-left: 0;
}

.message-flow__palette-accordion-item__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.palette__tree-item {
  @include carbon--type-style('label-01');

  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: content-box;
  width: 100%;
  min-height: $spacing-06;
  padding-bottom: $spacing-01;
  padding-left: $spacing-06;
  background-color: $ui-01;
  border: none;
  outline: none;

  // ~ .menu-bar__item {
  //   margin-left: $spacing-03;
  // }

  &:focus,
  &:hover {
    background-color: $ui-03;
  }
}
