@import '../styles/settings';

$block-class: 'flow-node';

$transition-speed: 0.3s;

.#{$block-class} {
  @include box-shadow;

  position: relative;
  min-width: $spacing-09;
  height: $spacing-09;
  background-color: $ui-01;

  // stylelint-disable-next-line carbon/motion-token-use
  transition: all $transition-speed ease-in-out;

  &:focus,
  &:hover {
    background-color: $ui-03;
    outline: none;
  }
}

.#{$block-class}--stadium,
.#{$block-class}--circle {
  border-radius: calc(#{$spacing-09} / 2);
}

.#{$block-class}--stadium,
.#{$block-class}--rectangle {
  padding-right: calc(var(--label-width) + #{$spacing-04});
}

.#{$block-class}--stadium,
.#{$block-class}--rectangle {
  width: 160px;
  // min-width: 160px; // force width to be 160 when content narrow
}

.#{$block-class}__img {
  display: inline-block;
  width: $spacing-06;
  height: $spacing-06;
  margin: $spacing-04;
}

.#{$block-class}__text {
  position: absolute;
  top: 50%;
  left: $spacing-09;
  display: flex;
  flex-direction: column;
  // align-items: center;
  color: $text-01;
  // stylelint-disable-next-line carbon/type-token-use
  line-height: $spacing-09;
  white-space: nowrap;
  // stylelint-disable-next-line carbon/layout-token-use
  transform: translate(0, -50%);
  // stylelint-disable-next-line carbon/motion-token-use
  transition: all $transition-speed ease-in-out;

  .#{$block-class}--square &,
  .#{$block-class}--circle & {
    // stylelint-disable-next-line carbon/type-token-use
    top: calc(100% + #{$spacing-03});
    left: 50%;
    // stylelint-disable-next-line carbon/layout-token-use
    transform: translate(-50%, 0);
  }
}

.#{$block-class}__name {
  position: relative;
}

.#{$block-class}__name--wide {
  @include carbon--type-style('label-01');
  @include carbon--font-weight('semibold');

  width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;

  .#{$block-class}--square &,
  .#{$block-class}--circle & {
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
  }

}

.#{$block-class}__name--narrow {
  @include carbon--type-style('label-01');

  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  visibility: hidden;
  opacity: 0;

  .#{$block-class}--square &,
  .#{$block-class}--circle & {
    visibility: visible;
    opacity: 1;
  }
}

.#{$block-class}__type {
  @include carbon--type-style('code-01');

  margin-top: $spacing-02;

  // stylelint-disable-next-line carbon/motion-token-use
  transition: all $transition-speed ease-in-out;

  .#{$block-class}--square &,
  .#{$block-class}--circle & {
    opacity: 0;
  }
}
