@import '../styles/settings';

.main-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  background-color: $ui-03;
  box-shadow: 0 0 0 0.25rem $ui-03;

  .toolbar-panel {
    background-color: $ui-03;
  }

  .main-toolbar__left {
    display: flex;
  }

  .main-toolbar__left-first {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    // margin-right: 60px;
  }
}
