@import '../styles/settings';

$block-class: 'flow-connector';

.#{$block-class} {
  --connector-color: #{$ui-04};
  --connected-color: #{$ui-04};

  display: flex;
  justify-content: center;
  width: 100px;
  height: $spacing-09;

  &.#{$block-class}--green:hover {
    --connected-color: #{$support-02};
  }

  &.#{$block-class}--red:hover {
    --connected-color: #{$text-error};
  }
}

.#{$block-class}__left,
.#{$block-class}__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.#{$block-class}__terminal {
  display: flex;
  flex-direction: column;
  width: $spacing-03;
  height: $spacing-03;
  margin: 20% 0;
  background-color: var(--connector-color);
}

.#{$block-class}:hover .#{$block-class}__terminal--connected {
  background-color: var(--connected-color);
}

.#{$block-class}__terminal--circle {
  border-radius: 50%;
}

.#{$block-class}__wires {
  position: relative;
  width: 72px;
}

.#{$block-class}__wire {
  position: absolute;
  display: inline-block;
  background-color: var(--connected-color);
}

.#{$block-class}__wire--left,
.#{$block-class}__wire--right {
  width: 50%;
  height: 1px;
}

.#{$block-class}__wire--middle {
  top: var(--mid-top);
  left: 50%;
  width: 1px;
  height: var(--mid-size);

  // stylelint-disable-next-line carbon/layout-token-use
  transform: translateX(-1px);
}

.#{$block-class}__wire--left {
  top: var(--left-top);
  left: 0;
}

.#{$block-class}__wire--right {
  top: var(--right-top);
  right: 0;
}
