@import '../styles/settings';

.toolbar {
  display: flex;
  align-items: center;
  min-height: $spacing-07;
  background-color: $ui-background;
  box-shadow: inset 0 -1px $ui-03;

}

.toolbar--right {
  justify-content: flex-end;;
}

.toolbar--empty {
  min-height: $spacing-02;
}

.toolbar__item {
  display: inline-flex;
  min-width: $spacing-06;
  height: $spacing-06;
  fill: $ui-04;
}

.toolbar__item--blank {
  margin: 0 $spacing-02;
  background-color: $ui-background;
}

.toolbar__label {
  @include carbon--type-style('label-01');

  display: flex;
  align-items: center;
  margin-left: $spacing-04;
  // margin-left: $spacing-03;
  color: $text-01;
  line-height: 28px;
}

.toolbar__button {
  display: inline-flex;
  // align-items: center;
  width: $spacing-06;
  height: $spacing-06;
  margin: 0;
  padding: $spacing-02;
  background-color: transparent;
  border: 0;

  svg {

    width: $spacing-05;
    height: $spacing-05;
  }

  &.toolbar__button--small svg {
    padding: $spacing-01;
  }
}

.palette__toolbar .toolbar__button:hover {
  background-color: $hover-ui;
}

.toolbar__tab {
  @include toolbar-hover;

  position: relative;
  display: inline-flex;
  // align-items: center;
  width: initial;
  // stylelint-disable-next-line carbon/layout-token-use
  height: 26px;
  margin: 0;
  padding: $spacing-02;
  color: $ui-04;
  background-color: transparent;
  border: 0;
  outline: none;

  &:focus,
  &:hover {
    background-color: $ui-03;
    outline: none;
  }
}

.toolbar__tab--active {
  box-shadow: 0 2px $interactive-01;
}
