@import '../styles/settings';

.root {
  width: 100%;
}

.tree-branch,
.tree-leaf {
  width: 100%; // calc(100% - #{$spacing-04});
  margin: 0;
  padding: $spacing-01 0 $spacing-01 $spacing-02;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
  text-overflow: ellipsis;
  background-color: transparent;
  border: 0;

  &:focus,
  &:hover {
    background-color: $ui-03;
    outline: none;
  }

  > svg {
    width: $spacing-05;
    height: $spacing-05;
    margin-right: $spacing-02;
    vertical-align: middle;
    transition: all $duration--fast-01 ease-in-out;
  }
}

.tree-leaf {
  padding-left: $spacing-04;
}

.root.tree-leaf {
  padding-left: $spacing-06;
}

.tree-branch__chevron {
  fill: $ui-05;
}

.tree-branch--open {
  > .tree-branch__chevron {
    transform: rotate(90deg);
  }
}

.tree-branch__content {
  width: 100%; /* calc(100% - #{$spacing-04}); */
  padding-left: $spacing-06;
}

.tree-branch + .tree-branch__content {
  max-height: 0;
  overflow: hidden;
  visibility: hidden;
}

.tree-branch--open + .tree-branch__content {
  max-height: 100%;
  overflow: hidden;
  visibility: visible;
}
