@import '../styles/settings';

.flow-exerciser {
  --flow-exerciser--zoom: 1;

  display: flex;
  flex-direction: column;
}

.flow-exerciser__container {
  display: block;
  flex: 1 1 100%;
  width: 100%;
  overflow: auto;
}

.flow-exerciser__content {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  height: 100%;
  padding: $layout-04;
  overflow: hidden;
  transform: scale(var(--flow-exerciser--zoom));
}

.flow-exerciser__image {
  width: 600px;
}

.flow-exerciser__test-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-shadow: inset 0 0 0 2px $interactive-01;
  visibility: hidden;
  opacity: 0;
  // stylelint-disable-next-line carbon/motion-token-use
  transition: all 0.3s ease-in-out;

  .flow-exerciser--test & {
    visibility: visible;
    opacity: 1;
  }

  .flow-exerciser__test-label {
    display: inline-block;
    padding: $spacing-02;
    color: $ui-02;
    background-color: $interactive-01;
  }
}

.flow-exerciser__popup {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: $spacing-05;
  background-color: $ui-01;
  border-top: 1px solid $interactive-01;
  box-shadow: 0 2.5px 5px $ui-03;
  // stylelint-disable-next-line carbon/layout-token-use
  transform: translate(-116px, -72px);

  .flow-exerciser__popup-heading {
    @include carbon--type-style('productive-heading-02');

    margin-bottom: $spacing-02;
  }

  .message-list {
    @include carbon--type-style('productive-heading-01');
  }

  .message-list .message-list {
    @include carbon--type-style('body-long-01');
  }

  .message-list .message-list__item {
    padding-left: $spacing-02;
  }

  .message-list .message-list .message-list__item {
    padding-left: $spacing-04;
  }

  .message-list__item-label {
    color: $text-05;
  }

  .message-list__item-data {
    display: inline-block;
    // @include carbon--font-weight("bold"); // didn't seem to work

    // stylelint-disable-next-line carbon/type-token-use
    font-weight: carbon--font-weight('semibold');
  }

  .message-list__item-label,
  .message-list__item-section {
    display: inline-flex;
    align-items: center;

    .message-list__arrow--open {
      transform: rotate(90deg);
    }
  }
}

.flow-exerciser__toolbar-button {
  @include toolbar-hover;

  margin: 0;
  color: $text-01;
  background-color: transparent;
  border: 0;
  fill: $disabled-03;
}


.flow-exerciser__toolbar-label {
  @include carbon--type-style('label-01');

  height: $spacing-06;
  margin-left: $spacing-04;
  // stylelint-disable-next-line carbon/type-token-use
  line-height: $spacing-06;
}

.flow-exerciser__toolbar-icon {
  @include toolbar-hover;

  width: $spacing-04;
  height: $spacing-04;
  fill: $disabled-03;
}

.flow-exerciser__toolbar {
  display: flex;
  justify-content: space-between;
  padding-left: $spacing-03;
}
