@import '../styles/settings';

.properties {
  display: flex;
  height: 100%;
}

.properties__sections {
  display: flex;
  flex-direction: column;
  width: 200px;
  box-shadow: inset 0 0 0 1px $ui-03;
}

.properties__section {
  height: $spacing-07;
  margin: 0;
  padding: $spacing-03 $spacing-04;
  text-align: left;
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid $ui-03;
  fill: $ui-04;

  &:focus {
    outline: 0;
  }

  &:hover {
    background-color: $ui-03;
  }

  &.properties__section--active {
    border-right: 1px solid $ui-01;
    box-shadow: inset 2px 0 0 $interactive-01;
  }
}

.properties__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  padding: $spacing-05;
}

.property {
  display: flex;
  align-items: flex-start;
  width: 100%;
  margin-bottom: $spacing-04;
}

.property__label {
  @include carbon--type-style('label-01');

  display: inline-block;
  flex: 0 0 $layout-07;
  height: $spacing-07;
  // stylelint-disable-next-line carbon/type-token-use
  line-height: $spacing-07;
}

.property__value {
  flex: 1 1 100%;
  height: $spacing-07;
  background-color: $ui-background;
  border: 0;
}
