@import '../styles/settings';

.tab-panel {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: $ui-01;

  // .tab-panel__content {
  //   background-color: $ui-01;
  // }
}

.tab-panel__header {
  display: flex;
  justify-content: space-between;
  background-color: $ui-03;
}

.tab-panel__tab-container {
  display: flex;
  justify-content: start;
}

.tab-top {
  display: flex;
  justify-content: space-between;
  padding: 3px 0;
  border-top: 1px solid transparent;

  &:hover {
    background-color: $hover-ui;
  }

  * {
    color: $disabled-03;
    fill: $disabled-03;
  }
}

.tab-top--active {
  background-color: $ui-background;
  border-top: 1px solid $interactive-01;

  * {
    color: $text-01;
    fill: $text-01;
  }
}

.tab-top__close {
  display: flex;
  align-items: center;
  margin: 0;
  background-color: transparent;
  border: 0;

  svg {
    width: $spacing-05;
    height: $spacing-05;
  }
}

.tab-top__title {
  @include carbon--type-style('label-01');

  display: inline-block;
  padding: $spacing-02;
}

.tab-panel__content {
  flex: 1 1 100%;
  max-height: calc(100% - #{$spacing-01} - #{$spacing-07} - 1px);
  background: $ui-01;
}

.tab-top__left {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0 $spacing-02;
  background-color: transparent;
  border: 0;
  outline: 0;
}

.tab-top__icon {
  width: $spacing-05;
  height: $spacing-05;
}

.tab-panel__size {
  display: flex;
  align-items: center;
}

.tab-panel__icon {
  @include toolbar-hover;

  width: $spacing-06;
  height: $spacing-06;
  padding: $spacing-02;
  fill: $disabled-03;
}
