@import '../styles/settings';

$palette-collapsed-size: 26px; /* 24 is too small with outline */
$transition: all $duration--moderate-02 motion(standard, productive);

.palette {
  overflow: hidden;
  // stylelint-disable-next-line carbon/motion-token-use
  transition: $transition;
}

.palette--collapsed {
  max-width: $palette-collapsed-size;
}

.palette__container {
  width: 100%;
  height: 100%;
  opacity: 1;

  // stylelint-disable-next-line carbon/motion-token-use
  transition: $transition;
}

.palette--collapsed .palette__container {
  min-width: $palette-width;
  // stylelint-disable-next-line carbon/layout-token-use
  margin-left: calc(-1 * #{$palette-width} + #{$palette-collapsed-size});
  overflow: hidden;
}

.message-flow__palette-accordion {
  opacity: 1;
  // stylelint-disable-next-line carbon/motion-token-use
  transition: $transition;
}

.palette--collapsed .message-flow__palette-accordion {
  opacity: 0;
}

.palette-title {
  @include carbon--type-style('label-01');

  display: flex;
  align-items: center;
  width: 100%;
  // stylelint-disable-next-line carbon/type-token-use
  line-height: 28px;
}

.palette-title__icon {
  margin: $spacing-01;
}

.palette__tree-item__text {
  margin-left: $spacing-03;
}

.palette__tree-item__icon {
  margin-right: $spacing-02;
}

.palette__toolbar-label {
  @include carbon--type-style('label-01');

  height: $spacing-06;
  margin-left: $spacing-04;
  // stylelint-disable-next-line carbon/type-token-use
  line-height: $spacing-06;
}

.palette__toolbar {
  display: flex;
  justify-content: space-between;
}

.palette__toolbar--inner {
  padding-right: $spacing-04;
  padding-left: $spacing-03;
}

.palette__toolbar-left {
  display: inline-flex;
}

.palette__toolbar-show-hide {
  margin-left: $spacing-03;
  // stylelint-disable-next-line carbon/motion-token-use
  transition: $transition;
}

.palette--collapsed .palette__toolbar-show-hide {
  margin-left: 0;
}

.palette-icon--hidden {
  display: none;
}
